  font: normal normal 18px/18px 'Icons', sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.share-button--vk::before {
  content: "\E900";
}
.share-button--facebook::before {
  margin: 0 0 0 1px;
  content: "\E902";
}
.share-button--telegram::before {
  content: "\E905";
}
.share-button--plus::before {
  content: "\E903";
}
.gallery {
  display: block;
  position: relative;
  width: 940px;
  height: 590px;
  margin: 60px auto;
  background: #f7f7f7;
  overflow: hidden;
}
.gallery__arrows {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  color: #fff;
  background: #1a1a1a;
  z-index: 2;
}
.gallery__arrows > div {
  display: block;
  padding: 10px 14px;
  cursor: pointer;
}
.gallery__items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  -webkit-transition: -webkit-transform .4s ease;
  transition: -webkit-transform .4s ease;
  transition: transform .4s ease;
  transition: transform .4s ease, -webkit-transform .4s ease;
}
.gallery__item {
  display: block;
  height: 100%;
}
.gallery__item img {
  display: block;
  max-height: 100%;
}
